body {
  color: #333;
  background-color: #f5f5f5;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

h1 {
  text-align: center;
  color: #0a2418;
  margin-top: 20px;
}

h2 {
  text-align: center;
  color: #2c3e50;
}

#auth-container {
  background: #fff;
  border-radius: 8px;
  max-width: 400px;
  margin: 20px auto;
  padding: 20px;
  box-shadow: 0 2px 10px #0000001a;
}

input {
  border: 1px solid #ccc;
  border-radius: 4px;
  width: calc(100% - 20px);
  margin: 10px 0;
  padding: 10px;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #3498db;
  border: none;
  border-radius: 4px;
  width: 100%;
  padding: 10px 15px;
}

button:hover {
  background-color: #2980b9;
}

#account-container {
  background: #fff;
  border-radius: 8px;
  max-width: 400px;
  margin: 20px auto;
  padding: 20px;
  box-shadow: 0 2px 10px #0000001a;
}

#transactions-list {
  padding: 0;
  list-style-type: none;
}

@media (width <= 600px) {
  body {
    padding: 10px;
  }

  #auth-container, #account-container {
    width: 100%;
    margin: 10px 0;
  }
}
